function getFallbackEvaluation(userAnswer: string, problem: any) {
  return {
    rating: 4,
    modelAnswer: problem?.modelAnswer || "Great job!",
    feedback: "",
    similarPhrases: [
      "Alternative expression 1",
      "Alternative expression 2",
      "Alternative expression 3"
    ]
  };
}